import React, { Component } from 'react';
import axios from 'axios';
import '../Login.css';
import { default as User } from '../features/login/User';
import Card from 'react-bootstrap/Card';
import CardDeck from 'react-bootstrap/CardDeck';
import Kakao from './kakaoJavaScriptAPIwrapper.js';
export class LoginCallBack extends Component {

  constructor(props) {
    super(props);
    const search = this.props.location.search;
    const params = new URLSearchParams(search);
    const code = params.get('code');
    this.state = { code: code };
  }

  componentDidMount() {
    axios.get('/api/kakao/login/loginCallBack?code=' + this.state.code).then((response) => {
      console.log(response);
      User.setUserLogin('true');
      User.setDataKey('id', response.data.result.id);
      User.setDataKey('email', response.data.result.kakao_account.email);
      User.setDataKey('nickname', response.data.result.kakao_account.profile.nickname);
      User.setDataKey('thumbnail', response.data.result.kakao_account.profile.thumbnail_image_url);
      //this.props.history.push('/');

      var k = new Kakao.KakaoSDK();
      k.loginWithKakaoPopUp();
    });
  }

  render() {

    return (
      <div className="text-center">
        <h1>Login</h1>
        <h2 className="h3 mb-3 font-weight-normal">{User.getDataKey("nickname")}님, 반갑습니다.</h2>
        <img src={User.getDataKey("thumbnail")} alt={User.getDataKey("nickname")}></img>
        <p></p>
        <CardDeck>
          <Card>
            <Card.Body>
              <Card.Text>
                LogOut
                <br />
                LogOut with Kakao
                <br />
                UnLink
                <br />
              </Card.Text>
            </Card.Body>
          </Card>
          <Card>
            <Card.Body>
              <Card.Text>
                카카오 링크
                <br />
                나에게 기본 메세지 발송
                <br />
                나에게 스크랩 메세지 발송
                <br />
                나에게 사용자 정의 메세지 발송
                <br /><br />
                카카오톡 API
                <br />
                나에게 기본 메세지 발송
                <br />
                나에게 스크랩 메세지 발송
                <br />
                나에게 사용자 정의 메세지 발송
              </Card.Text>
            </Card.Body>
          </Card>
          <Card>
            <Card.Body>
              <Card.Text>
                카카오 링크 전송 성공 콜백
              </Card.Text>
            </Card.Body>
          </Card>
        </CardDeck>
        <p></p>
        <CardDeck>
          <Card>
            <Card.Body>
              <Card.Text>
                고객목록 (관리자)
              </Card.Text>
            </Card.Body>
          </Card>
          <Card>
            <Card.Body>
              <Card.Text>
                친구목록 (로그인한 사용자)
              </Card.Text>
            </Card.Body>
          </Card>
          <Card>
            <Card.Body>
              <Card.Text>

              </Card.Text>
            </Card.Body>
          </Card>
        </CardDeck>        
      </div>
    );
  }
}

