export default class KakaoSDK {

    JAVASCRIPT_KEY = "2d68640b56d986af5c8a48505c7c8c71";
    REST_API_KEY = "4408b5bb51bdf4c89879e933556a21e8";
    CLIENT_SECRET = "QZhr9itOs0mxVRDxIvuOfOLzjZMc5q1U";
    ADMIN_KEY = "72462462f6fc9baad63f2de2ad3d865b";
    REDIRECT_URI = "72462462f6fc9baad63f2de2ad3d865b";
    Kakao = {};

    constructor() {
        const Kakao = document.createElement("script");
        Kakao.src = "/static/libs/your_script.js";
        Kakao.async = true;
        Kakao.onload = () => this.scriptLoaded();
        document.body.appendChild(Kakao);
        
        this.Kakao.init(this.JAVASCRIPT_KEY);
        console.log(this.Kakao.isInitialized());
    }

    loginWithKakao() {
        this.Kakao.Auth.authorize({
            redirectUri: window.location.origin+'/'+this.REDIRECT_URI
        });
    }    

    loginWithKakaoPopUp() {
        this.Kakao.Auth.login({
            success: function(authObj) {
                console.log(authObj);
                this.Kakao.Auth.setAccessToken(authObj.access_token);
                //★ 추가 할 것 : 로그인 성공 후 처리 
            },
            fail: function(err) {
                console.log(err)
            }
        })
    }    

    logoutWithKakao() {
        if (!this.Kakao.Auth.getAccessToken()) {
            console.log('Not logged in.');
            return;
        }
        console.log("before Logout:"+this.Kakao.Auth.getAccessToken());
        this.Kakao.Auth.logout(function() {
            console.log("after Logout:"+this.Kakao.Auth.getAccessToken()); 
            //★ 추가 할 것 : 로그아웃 성공 후 처리 
        });
    }    

    unlinkWithKakao() {
        this.Kakao.API.request({
            url: '/v1/user/unlink',
            success: function(response) {
                console.log(response);
            },
            fail: function(error) {
                console.log(error);
            }
        });
    }    

    profileWithKakao() {
        this.Kakao.API.request({
            url: '/v2/user/me',
            success: function(response) {
                console.log(response);
                document.getElementById("userid").innerText = response.id;
                document.getElementById("nickname").innerText = response.kakao_account.profile.nickname;
                document.getElementById("profile_image").src = response.properties.profile_image;
                document.getElementById("thumbnail_image").src = response.properties.thumbnail_image;
            },
            fail: function(error) {
                console.log(error);
            }
        });
    }    
}




